<template>
  <div id="air" @click="packUp">
      <router-link to="/airQuery" id="airQuery" class="queryNav">主页</router-link>
      <router-link to="/airLineQuery" id="airLineQuery" class="queryNav">++</router-link>
      <router-link to="/cityQuery" id="cityQuery" class="queryNav">++</router-link>
      <router-link to="/setting" id="setting" class="queryNav">++</router-link>
      <router-view :close="shut"></router-view>
      <div class="pla-set-clear" @click="clearAll">&#xe64e;</div>
  </div>
</template>
<script>
export default {
    name:'air',
    data () {
        return {
            shut:true
        }
    },
    methods:{
        packUp:function () {
            this.shut=!this.shut;
        },
        clearAll:function () {
            document.getElementById('air').style.zIndex=-10;
        }
    },
    components:{

    }
}
</script>

<style lang="less">

</style>



