import {
	INCREMENT,
	DECREMENT,
    CHANGE,
    PACKUP
} from './types'
import getters from './getters'
import {state} from './state.js'
const mutations = {
    [PACKUP](){
        state.yous++;
    },

};

export default {
	state,
	mutations,
	getters
}


